@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.2/css/all.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.TopBar {
    /*orange: #e95420;
    red: #df382c;
    grey: #aea79f*/
    width: 100%;
    height: 60px;
    background-color: #e95420;
    color: white;
}
img {
    position: absolute;
    top: 10px;
    right: 20px;
    height: 40px;
    width: 40px;
}
h2 {
    margin: 0;
    padding: 0;
    position: absolute;
    top: 15px;
    left: 100px;
}
.App {
  text-align: center;
}


.av-f-c {
    position: relative;
    width: 840px;
    margin: 0 auto;
}

.AvFlightsContainer {
    width: 840px;
    height: 250px;
    margin: auto;
    background-color: #d9d9d9;
    overflow:scroll;
}
.category-desc {
    margin: 0px auto 0 auto;
    padding: 10px 0;
    width: 840px;
    background-color: #aea79f;
    color: white;
    font-weight: bold;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: 1fr;
    list-style: none;
    grid-gap: 10px;
}
.category-desc li:first-of-type {
    grid-column: 1 / 2;
    grid-row: 1;
}
.category-desc li:nth-of-type(2) {
    grid-column: 2 / 3;
    grid-row: 1;
}
.category-desc li:nth-of-type(3) {
    grid-column: 3 / 4;
    grid-row: 1;
}
.category-desc li:nth-of-type(4) {
    grid-column: 4 / 5;
    grid-row: 1;
}
.category-desc li:nth-of-type(5) {
    grid-column: 5 / 6;
    grid-row: 1;
}
.flights-list {
    overflow: scroll;
}
h3 {
    position: relative;
    right: 300px;
    padding: 5px;
    margin: 10px 0 10px 0;
    font-size: 16px;
    color: black;
}
.new-flights-btn {
    position: absolute;
    top: 2.5px;
    right: 0;
    background-color: #f2f2f2;
    color: black;
    border: none;
    font-weight: bold;
    border-radius: 5px;
    padding: 5px 10px;
    font-size: 12px;
}
.new-flights-btn:hover {
    cursor: pointer;
    background-color: #e95420;
    color: white;
}
.new-flights-btn span img {
    display: inline;
    height: 20px;
    width: 20px;
}

@media (max-width: 500px) {
    .av-f-c {
        width: 300px;
    }
    .AvFlightsContainer {
        width: 300px;
        overflow: scroll;
    }
    .category-desc {
        width: 300px;
        overflow: scroll;
    }
}
.Flight {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: repeat(5, 1fr);
    margin: 5px;
    padding: 0;
    background-color: #f2f2f2;
    font-size: 12px;
    overflow: scroll;
    font-weight: bold;
}
.Flight:hover {
    cursor: pointer;
    background-color:  white;
    color: black;
}
.Flight p:first-of-type {
    grid-column: 1 / 2;
    grid-row: 1;
}
.Flight p:nth-of-type(2) {
    grid-column: 2 / 3;
    grid-row: 1;
}
.Flight p:nth-of-type(3) {
    grid-column: 3 / 4;
    grid-row: 1;
}
.Flight p:nth-of-type(4) {
    grid-column: 4 / 5;
    grid-row: 1;
}
.Flight p:last-of-type {
    grid-column: 5 / 6;
    grid-row: 1;
}
.Flight:active {
    background-color: #e95420;
    color: white;
}

@media (max-width: 500px) {
    .Flight {
        width: 500px;
    }
}
.SelectFlightContainer {
    width: 840px;
    height: 350px;
    background-color: #f2f2f2;
    position: relative;
    margin: 20px auto;
}
.SelectedFlight {
    position: absolute;
    top: 0;
    right: 0;
    height: 40px;
    width: 100%;
    background: lightslategrey;
}
.passenger-label {
    width: 140px;
    grid-row: 1;
    grid-column: 1 / 2;
}
.bag-label {
    grid-row: 1;
    grid-column: 2 / 3;
    width: 140px;
}
.carry-label {
    grid-row: 1;
    grid-column: 3 / 4;
    width: 140px;
}
.class-label {
    grid-row: 1;
    grid-column: 4 / 5;
    width: 140px;
}
.price-label {
    grid-row: 1;
    grid-column: 5 / 6;
    width: 140px;
}
.controls2 {
    grid-row: 2;
    grid-column: 1 / 6;
}
.controls3 {
    grid-row: 3;
    grid-column: 1 / 6;
    background-color: peru;
}
.btns-container {
    margin: 0 0px 0 0px;
}
.btns-container button {
    font-size: 18px;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    background-color: #aea79f;
    color: white;
    width: 25px;
    margin: 2.5px;
}
.btns-container button:hover {
    cursor: pointer;
    transform: scale(1.1);
    background-color: #df382c;
}
.btns-container1 {
    grid-row: 3;
    grid-column: 1 / 2;
    width: 140px;
}
.btns-container2 {
    grid-row: 3;
    grid-column: 2 / 3;
    width: 140px;
}
.btns-container3 {
    grid-row: 3;
    grid-column: 3 / 4;
    width: 140px;
}
.btns-container4 {
    grid-row: 3;
    grid-column: 4 / 5;
    width: 140px;
}
/* .controls .btns-container:first-of-type {
    margin: 0 0 0 20px;
} */
.Bookbtn {
    margin: auto;
    font-size: 20px;
    padding: 10px;
    background-color: #17a2b8;
    font-weight: bold;
    font-size: 26px;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
}
.Bookbtn:hover {
    cursor: pointer;
    transform: scale(1.05);
}
.X {
    background-color: transparent;
    font-size: 16px;
    width: 5%;
    height: 16px;
    position: absolute;
    top: 0;
    right: 0;
    margin: 0 !important;
    padding: 15px 0;
}
.X:hover {
    cursor: pointer;
    transform: scale(1.2);
}
.selFlight {
    width: 100%;
    height: 20px;
    background-color: #e95420;
    color: white;
    font-weight: bold;
    display: flex;
    padding: 15px 0;
}
.selFlight div {
    margin: 0px 20px 0 20px;
}
.selFlight .img1 {
    width: 80px;
    height: 80px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation-name: move-plane;
    animation-duration: 0.5s;
}
@keyframes move-plane {
    from {transform: translate(-1000%, -50%)}
    to {transform: translate(-50%, -50%)}
}



.please-select-flight {
    color: black;
    text-align: center;
    margin: 90px auto;
    padding: 0;
    position: relative;
    font-size: 16px;
}
/* .options-price {
    font-size: 14px;
    grid-row: 4;
    grid-column: 1 / 10;
    background-color: powderblue;
    display: flex;

} */
.additional-passengers {
    grid-row: 4;
    grid-column: 1 / 2;
    width: 140px;
    font-size: 12px;
    margin-top: 0px;
}
.additional-bags {
    grid-row: 4;
    grid-column: 2 / 3;
    width: 140px;
    font-size: 12px;
    margin-top: 0px;
}
.additional-carry {
    grid-row: 4;
    grid-column: 3 / 4;
    width: 140px;
    font-size: 12px;
    margin-top: 0px;
}
.class-prices {
    grid-row: 4;
    grid-column: 4 / 5;
    width: 140px;
    font-size: 12px;
    margin-top: 0px;
}
.class-prices p {
    margin: 0;
    padding: 0;
}
.controls-container {
    height: 200px;
    z-index: 100;
    width: 90%;
    margin: 20px auto 10px auto;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-gap: 25px 10px;
    gap: 25px 10px;
}
.fade {
    opacity: 1;
    animation: fade-in;
    animation-duration: 1s;
    animation-fill-mode: forwards;
}
@keyframes fade-in {
    from {opacity: 0}
    to {opacity: 1}
}

@media (max-width: 500px) {
    .SelectFlightContainer {
        width: 300px;
        overflow: scroll;
    }
    .fade {
        width: 300px;
        overflow: scroll;
    }
    .selFlight {
        width: 820px;
    }
}
.FlightOptions {
    display: grid;
    grid-gap: 10px;
    gap: 10px;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: 1fr;
    font-weight: bold;
    color: black;
    font-size: 20px;
}
.passengers {
    grid-row: 1;
    grid-column: 1 / 2 ;
    width: 140px;
}
.bags {
    grid-row: 1;
    grid-column: 2 / 3;
    width: 140px;
}
.carryOn {
    grid-row: 1;
    grid-column: 3 / 4;
    width: 140px;
}
.class {
    grid-row: 1;
    grid-column: 4 / 5;
    width: 140px;
}
.price-option {
    grid-row: 1;
    grid-column: 5 / 6;
    width: 140px;
    color: #17a2b8;
    font-size: 20px;
}

.Modal {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.7);
    z-index: 100;
}
.Modal-content {
    height: 150px;
    width: 300px;
    background-color: white;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 20px;
    font-weight: bold;
}
.Modal-content button {
    font-size: 16px;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    background-color: #aea79f;
    color: white;
    margin: 2.5px;
    width: 50px;
    padding: 10px;
}
.Modal-content button:hover {
    transform: scale(1.1);
    background-color: #df382c;
    cursor: pointer;
}

.BookedFlights {
    width: 920px;
    height: 350px;
    background-color: #d9d9d9;
    margin: 40px auto;
    position: relative;
    overflow: scroll;
}
.bookedflights-list {
    width: 91%;
    left: 50%;
    transform: translateX(-50%);
    position: absolute;
    top: 60px;
    
}
.bookedflights-list ul {
    list-style: none;
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: repeat(8, 1fr);
    justify-content: space-between;
    margin: 0;
    padding: 0;
    font-size: 14px;
}
.bookedflights-list ul li:first-of-type {
    grid-column: 1 / 2;
    grid-row: 1;
}
.bookedflights-list ul li:nth-of-type(2) {
    grid-column: 2 / 3;
    grid-row: 1;
}
.bookedflights-list ul li:nth-of-type(3) {
    grid-column: 3 / 4;
    grid-row: 1;
}
.bookedflights-list ul li:nth-of-type(4) {
    grid-column: 4 / 5;
    grid-row: 1;
}
.bookedflights-list ul li:nth-of-type(5) {
    grid-column: 5 / 6;
    grid-row: 1;
}
.bookedflights-list ul li:nth-of-type(6) {
    grid-column: 6 / 7;
    grid-row: 1;
}
.bookedflights-list ul li:nth-of-type(7) {
    grid-column: 7 / 8;
    grid-row: 1;
}
.bookedflights-list ul li:nth-of-type(8) {
    grid-column: 8 / 9;
    grid-row: 1;
}

@media (max-width: 500px) {
    .BookedFlights {
        width: 300px;
        overflow: scroll;
    }
}
.BookedFlight {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: 1;
    background-color: #f2f2f2;
    color: black;
    padding: 5px 5px;
    margin: 5px 0;
    font-size: 12px;
    font-weight: bold;
    position: relative;
}
.BookedFlight p:first-of-type {
    grid-column: 1 / 2;
    grid-row: 1;
}
.BookedFlight p:nth-of-type(2) {
    grid-column: 2 / 3;
    grid-row: 1;
}
.BookedFlight p:nth-of-type(3) {
    grid-column: 3 / 4;
    grid-row: 1;
}
.BookedFlight p:nth-of-type(4) {
    grid-column: 4 / 5;
    grid-row: 1;
}
.BookedFlight p:nth-of-type(5) {
    grid-column: 5 / 6;
    grid-row: 1;
}
.BookedFlight p:nth-of-type(6) {
    grid-column: 6 / 7;
    grid-row: 1;
}
.BookedFlight p:nth-of-type(7) {
    grid-column: 7 / 8;
    grid-row: 1;
}
.BookedFlight p:nth-of-type(8) {
    grid-column: 8 / 9;
    grid-row: 1;
}
.BookedFlight:hover {
    background-color: #df382c;
    color: white;
    cursor: pointer;
}

.bFlight-X {
    position: absolute;
    top: 9px;
    right: 0px;
    font-size: 24px;
    z-index: -1;
    color: #df382c;
    font-weight: normal;
}

.BookedFlight:hover .bFlight-X {
    z-index: 0;
    animation: expand;
    animation-duration: 0.3s;
    animation-fill-mode: forwards;
}

@keyframes expand {
    from {transform: translateX(0%)}
    to {transform: translateX(200%)}
}

@media (max-width: 500px) {
    .BookedFlight {
        width: 850px;
    }
}
